'use client'

import { useState } from 'react'

interface Video {
  id: string
  title: string
  url: string
  thumbnail?: string
  uploadedAt: string
  duration?: string
}

interface VideoCardProps {
  video: Video
}

export default function VideoCard({ video }: VideoCardProps) {
  const [isPlaying, setIsPlaying] = useState(false)
  const [error, setError] = useState(false)

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    return date.toLocaleDateString('en-US', { 
      month: 'short', 
      day: 'numeric',
      year: 'numeric'
    })
  }

  return (
    <div className="bg-white rounded-2xl shadow-sm border border-gray-100 overflow-hidden hover:shadow-md transition-all duration-300">
      {/* Video Player Area */}
      <div className="relative aspect-video bg-gradient-to-br from-gray-900 to-gray-700">
        {!error ? (
          <>
            <video
              className="w-full h-full object-cover"
              controls={isPlaying}
              onPlay={() => setIsPlaying(true)}
              onPause={() => setIsPlaying(false)}
              onError={() => setError(true)}
              poster={video.thumbnail}
            >
              <source src={video.url} type="video/mp4" />
              Your browser does not support the video tag.
            </video>
            
            {!isPlaying && (
              <div className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-20">
                <button 
                  className="w-16 h-16 bg-white bg-opacity-90 rounded-full flex items-center justify-center hover:bg-opacity-100 transition-all duration-200 shadow-lg"
                  onClick={() => setIsPlaying(true)}
                >
                  <div className="w-0 h-0 border-l-[16px] border-l-black border-t-[12px] border-t-transparent border-b-[12px] border-b-transparent ml-1"></div>
                </button>
              </div>
            )}
            
            {video.duration && (
              <div className="absolute bottom-3 right-3 bg-black bg-opacity-70 text-white text-xs px-2 py-1 rounded-md font-medium">
                {video.duration}
              </div>
            )}
          </>
        ) : (
          <div className="w-full h-full flex items-center justify-center">
            <div className="text-center text-white">
              <div className="w-12 h-12 bg-white bg-opacity-20 rounded-full flex items-center justify-center mb-3 mx-auto">
                <div className="w-6 h-6 bg-white rounded-full"></div>
              </div>
              <p className="text-sm opacity-80">Video unavailable</p>
            </div>
          </div>
        )}
      </div>

      {/* Video Info */}
      <div className="p-4">
        <div className="flex items-start justify-between">
          <div className="flex-1">
            <h3 className="font-semibold text-gray-900 text-lg mb-1 line-clamp-2">
              {video.title}
            </h3>
            <p className="text-gray-500 text-sm">
              Uploaded {formatDate(video.uploadedAt)}
            </p>
          </div>
          
          <div className="ml-4 flex space-x-2">
            <button className="w-8 h-8 bg-gray-100 rounded-full flex items-center justify-center hover:bg-gray-200 transition-colors">
              <div className="w-4 h-4 bg-gray-600 rounded-full"></div>
            </button>
            <button className="w-8 h-8 bg-gray-100 rounded-full flex items-center justify-center hover:bg-gray-200 transition-colors">
              <div className="w-1 h-4 bg-gray-600 rounded-full"></div>
              <div className="w-1 h-4 bg-gray-600 rounded-full ml-1"></div>
              <div className="w-1 h-4 bg-gray-600 rounded-full ml-1"></div>
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}
