'use client'

import { useState, useEffect } from 'react'
import VideoCard from '../components/VideoCard'
import LoadingSpinner from '../components/LoadingSpinner'

interface Video {
  id: string
  title: string
  url: string
  thumbnail?: string
  uploadedAt: string
  duration?: string
}

export default function HomePage() {
  const [videos, setVideos] = useState<Video[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')

  useEffect(() => {
    const fetchVideos = async () => {
      try {
        const response = await fetch('/api/videos')
        const result = await response.json()
        
        if (result.success) {
          setVideos(result.data)
        } else {
          setError(result.error || 'Failed to load videos')
        }
      } catch (err) {
        setError('Failed to load videos')
      } finally {
        setLoading(false)
      }
    }

    fetchVideos()
  }, [])

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <LoadingSpinner />
      </div>
    )
  }

  if (error) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen px-6">
        <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mb-4">
          <div className="w-8 h-8 bg-red-500 rounded-full"></div>
        </div>
        <h2 className="text-xl font-semibold text-gray-900 mb-2">Something went wrong</h2>
        <p className="text-gray-600 text-center">{error}</p>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-white to-gray-50">
      {/* Header */}
      <div className="bg-white border-b border-gray-100">
        <div className="px-6 py-8">
          <div className="flex items-center justify-between mb-2">
            <h1 className="text-2xl font-bold text-gray-900">Your Videos</h1>
            <div className="w-10 h-10 bg-black rounded-full flex items-center justify-center">
              <div className="w-6 h-6 bg-white rounded-full"></div>
            </div>
          </div>
          <p className="text-gray-600">Manage and view your personal collection</p>
        </div>
      </div>

      {/* Content */}
      <div className="px-6 py-6">
        {videos.length === 0 ? (
          <div className="flex flex-col items-center justify-center py-16">
            <div className="w-20 h-20 bg-gray-100 rounded-2xl flex items-center justify-center mb-6">
              <div className="w-10 h-10 bg-gray-300 rounded-lg"></div>
            </div>
            <h3 className="text-xl font-semibold text-gray-900 mb-2">No videos yet</h3>
            <p className="text-gray-600 text-center mb-6">
              Start building your collection by adding your first video from your profile
            </p>
            <div className="w-full max-w-xs">
              <div className="bg-black text-white py-3 px-6 rounded-xl text-center font-medium">
                Go to Profile
              </div>
            </div>
          </div>
        ) : (
          <div className="space-y-4">
            {videos.map((video) => (
              <VideoCard key={video.id} video={video} />
            ))}
          </div>
        )}
      </div>
    </div>
  )
}
